<template>
  <div class="col-md-4">
    <label class="control-label"> Ciudad </label>
    <div class="form-group" :class="[classValidate]">
      <div class="mb-0">
        <input class="form-control" v-model="fieldData" @change="validate" @keyup="searchCity" autocomplete="nope">
      </div>
    </div>
    <label class="error" style="color:#f03333" v-if="error">{{error}}</label>
    <div role="alert" class="alert alert-success" id="alert" v-if="success">
      <span data-notify="message"><span>{{success}}</span></span>
    </div>
    <div v-if="cities" class="dropdown" :class="show" >
      <div class="dropdown-menu show" aria-labelledby="dropdownMenuButton" style="position: inherit;" >
        <div v-for="city in cities" :key="city.code">
          <a class="dropdown-item" style="cursor: pointer;" @click="saveCity(city.code, city.name)" >
            {{ city.name }} | {{ city.department.name }}
          </a>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import httpClient from "@/plugins/AxiosGlobal";

export default {
  name: "City",
  data(){
    return {
      fieldData: this.$store.state.Auth.user.city.name,
      error: null,
      success: null,
      classValidate: null,
      cities: null,
      codeCity: null,
      show: 'null'
      // responseError: null
    }
  },
  methods: {
    async validate() {
        let regex = /(\d+)/g;
        if (this.fieldData.match(regex)) {
          this.classValidate = 'has-danger'
          this.success = null;
          return this.error = 'La ciudad no puede tener números'
        }

        if (!this.fieldData) {
          this.classValidate = 'has-danger'
          this.success = null;
          return this.error = 'El campo no puede estar vacío'
        }

    },
    async searchCity() {
      if (this.fieldData) {
        this.show = 'show';
        await httpClient.get('/cities/'+this.fieldData).then(response => { this.cities = response.data; }).catch(() => { })
      }
    },
    async saveCity(city, cityName) {
        this.show = null;
        if (city) {
          await httpClient
            .post('/user/profile',  { city_id: city})
            .then(() => {
              this.error = null;
              this.classValidate = 'has-success'
              this.success = `El campo ciudad ha sido guardado correctamente`
              this.$store.dispatch('Auth/getUser');
              setTimeout(() => this.success = null, 3000);
              this.$store.dispatch('Notification/getNotification', {
                message: `La ciudad ha sido guardado correctamente`,
                position: 'top-center',
                icon: 'icon-bell-55',
                status: 'alert-success',
                static: true
              });
              this.fieldData = cityName;
              this.show = null;
            })
            .catch(err => {
              this.classValidate = 'has-danger'
              this.success = null;
              this.error = err.response.data.errors.city[0];
              this.$store.dispatch('Notification/getNotification', {
                message: `La ciudad NO ha sido guardado correctamente`,
                position: 'top-center',
                icon: 'icon-bell-55',
                status: 'alert-danger'
              });
            })
      }
    }
  }
}
</script>

<style scoped>

#alert{
  font-size: 10px;
  margin: 0;
  padding: 3px;
}

</style>
