<template>
  <div class="col-md-4">
    <label class="control-label"> {{labelName}} </label>
    <div class="form-group" :class="[classValidate]">
      <div class="mb-0">
        <input :type="fieldType" class="form-control" v-model="fieldData" @change="validate">
      </div>
    </div>
    <label class="error" style="color:#f03333" v-if="error">{{error}}</label>
    <div role="alert" class="alert alert-success" id="alert" v-if="success">
     <span data-notify="message"><span>{{success}}</span></span>
    </div>
  </div>
</template>

<script>
import httpClient from "@/plugins/AxiosGlobal";

export default {
  name: "generic",
  props: ['fieldName', 'labelName', 'fieldType'],
  data(){
    return {
      fieldData: null,
      error: null,
      success: null,
      classValidate: null,
      responseError: null
    }
  },
  created: function () {
    switch(this.fieldName) {
      case 'name':
        this.fieldData = this.$store.state.Auth.user.name
        break;
      case 'phone':
        this.fieldData = this.$store.state.Auth.user.phone
        break;
      case 'area':
        this.fieldData = this.$store.state.Auth.user.area
        break;
      case 'address':
        this.fieldData = this.$store.state.Auth.user.address
        break;
      default:
        this.fieldData = this.$store.state.Auth.user.name
    }

  },
  methods: {
    async validate() {

      if (!this.fieldData) {
        this.classValidate = 'has-danger'
        this.success = null;
        return this.error = 'El campo no puede estar vacío'
      }

      if (this.fieldName === 'name') {
        let regex = /(\d+)/g;
        if (this.fieldData.match(regex)) {
          this.classValidate = 'has-danger'
          this.success = null;
          return this.error = 'El nombre no puede tener números'
        }
      }

      if (this.fieldName === 'phone') {
        if(isNaN(this.fieldData)){
          return this.error = 'El Celular solo puede ser numérico';
        }
        if (this.fieldData.length !== 10) {
          return this.error = 'El celular debe contener 10 números';
        }
      }

      this.success = null

      if (this.fieldData) {
        await httpClient.post('/user/profile',  {[this.fieldName]: this.fieldData})
                        .then(() => {
                          this.error = null;
                          this.classValidate = 'has-success'
                          this.success = `El campo ${this.labelName} ha sido guardado correctamente`
                          this.$store.dispatch('Auth/getUser');
                          setTimeout(() => this.success = null, 3000);
                          this.$store.dispatch('Notification/getNotification', {
                            message: `${this.labelName} ha sido guardado correctamente`,
                            position: 'top-center',
                            icon: 'icon-bell-55',
                            status: 'alert-success',
                            static: true
                          });
                        })
                        .catch(err => {
                          this.classValidate = 'has-danger'
                          this.success = null;
                          if (this.fieldName === 'name') {
                            this.error = err.response.data.errors.name[0];
                          }
                          if (this.fieldName === 'phone') {
                            this.error = err.response.data.errors.phone[0];
                          }
                          this.$store.dispatch('Notification/getNotification', {
                            message: `${this.labelName} NO ha sido guardado correctamente`,
                            position: 'top-center',
                            icon: 'icon-bell-55',
                            status: 'alert-danger'
                          });
                        })
      }
    },
  }
}
</script>

<style scoped>
  #alert{
    font-size: 10px;
    margin: 0;
    padding: 3px;
  }

</style>
